html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: "SourceSansPro", sans-serif;
  font-weight: 400;
  font-size: 25px;
  color: var(--color-main);
  background-color: var(--color-backgraund);
}

img {
  max-width: 100%;
}

a {
  text-decoration: none;
  color: inherit;
}

.visually-hidden
{
    position: absolute !important;

    overflow: hidden;
    clip: rect(1px 1px 1px 1px);
    clip: rect(1px, 1px, 1px, 1px);
    width: 1px;
    height: 1px; 
    padding: 0;
    border: 0;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.btn-reset {
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer;
}

.site-container {
  overflow: hidden;
}

.container {
  margin: 0 auto;
  padding: 0 15px;
  width: var(--max-width-container);
}

@media(max-width: 1410px){
    .container {
    margin: 0 auto;
    padding: 0 15px;
    max-width: 1284px; 
  }
}

@media(max-width: 1284px){
  .container {
    margin: 0 auto;
    padding: 0 15px;
    max-width: 1130px;
  }
}

@media (max-width: 1130px){
  .container {
    margin: 0 auto;
    padding: 0 15px;
    max-width: 930px;
  }
}


@media (max-width: 930px){
  .container {
    margin: 0 auto;
    padding: 0 15px;
    max-width: 610px;
  }
}

@media (max-width: 630px){
  .container {
    margin: 0 auto;
    padding: 0 15px;
    max-width: 380px;
  }
}

@media (max-width: 400px){
  .container {
    margin: 0 auto;
    padding: 0 10px;
    max-width: 300px;
  }
}